  const searchAutoCompleteCreatedBy = async (event: any) => {
    let suggestionsTemplate = [];
    if (!event.query.trim().length) {
      suggestionsTemplate = createdByTemplate;
    } else {
      await UserService.getAll(0, 10, `name:lk=${event.query}&`).then((res) => {
        if (res.status === 200) {
          suggestionsTemplate = [...res.data.results];
          // setIsLoading(false);
        }
      });
      if (suggestionsTemplate.length === 0) {
        suggestionsTemplate.push({ name: undefined, designation: undefined, emailId: undefined });
      }
    }
    setSuggestionsTemplate(suggestionsTemplate);
  };

