  const onSearch = () => {
    let search = "";
    if (searchObj.entity) {
      search = `${search}entity:lk=${searchObj.entity}&`;
    }
    if (searchObj.name) {
      search = `${search}name:lk=${searchObj.name}&`;
    }
    if (searchObj.maker) {
      search = `${search}maker:lk=${searchObj.maker}&`;
    }
    if (searchObj.createdFrom) {
      console.log(searchObj.createdFrom);
      search = `${search}createdAt:gt=${convertDateToISO(searchObj.createdFrom)}&`;
    }
    if (searchObj.createdTo) {
      search = `${search}createdAt:lt=${convertDateToISO(searchObj.createdTo)}&`;
    }
    if (searchObj.checkedFrom) {
      search = `${search}updatedAt:gt=${convertDateToISO(searchObj.checkedFrom)}&`;
    }
    if (searchObj.checkedTo) {
      search = `${search}updatedAt:lt=${convertDateToISO(searchObj.checkedTo)}&`;
    }
    if (searchObj.operation) {
      search = `${search}operation:lk=${searchObj.operation}&`;
    }
    if (searchObj.checker?.id) {
      search = `${search}&checker=${searchObj.checker?.id}&`;
    }
    if (searchObj.status) {
      search = `${search}status:lk=${searchObj.status}&`;
    }
    setDataTable({
      ...dataTable,
      search,
    });
  };
